
#app{
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto ;
}
.header{
  grid-row-start: 1;
  grid-row-end: 1;
  min-height: 0
}
.content{
  grid-row-start: 2;
  grid-row-end: 3;
  margin-bottom: 20px;
}
.footer {
  grid-row-start: 3;
  grid-row-end: 4;
}

